import React, { useState } from 'react';
import './App.scss';
import { GoodsList } from './GoodsList';
import { Good } from './types/Good';
import { getAll, get5First, getRedGoods } from './api/goods';

export const App: React.FC = () => {
  const [goods, setGoods] = useState<Good[]>([]);

  const handleAllGoods = () => {
    getAll().then(chosenGoods => setGoods(chosenGoods));
  };

  const handle5FirstGoods = () => {
    get5First().then(chosenGoods => setGoods(chosenGoods));
  };

  const handleRedGoods = () => {
    getRedGoods().then(chosenGoods => setGoods(chosenGoods));
  };

  return (
    <div className="App">
      <h1>Dynamic list of Goods</h1>

      <button
        type="button"
        data-cy="all-button"
        onClick={handleAllGoods}
      >
        Load all goods
      </button>

      <button
        type="button"
        data-cy="first-five-button"
        onClick={handle5FirstGoods}
      >
        Load 5 first goods
      </button>

      <button
        type="button"
        data-cy="red-button"
        onClick={handleRedGoods}
      >
        Load red goods
      </button>

      {(GoodsList.length > 0)
        && <GoodsList goods={goods} />}

    </div>
  );
};
